<template>
  <div>
    <timer
      v-if="!complete"
      :time="nextTimer"
    />
    <h1>
      –ü—Ä–æ—Å—Ç–æ–π –∏ –≤–∫—É—Å–Ω—ã–π —Ä–µ—Ü–µ–ø—Ç <strong>–ü–∏—Ü—Ü—ã</strong>
    </h1>
    <part
      v-for="(part, index) in parts"
      :key="part.title"
      :title="part.title"
      :description="part.description"
      :complete="part.complete"
      @click="handleClick(index)"
    />
    <p v-if="!complete">{{totalSteps}} –∏–∑ {{parts.length}} –≥–æ—Ç–æ–≤–æ</p>
    <p v-else>–í—É–∞–ª—è</p>
  </div>
</template>
<script>
import RecipePart from './RecipePart';
import Timer from './Timer';

export default {
  data() {
    return {
      parts: [
        {
          title: '–ü–æ–¥–≥–æ—Ç–æ–≤–∏—Ç—å —Ç–µ—Å—Ç–æ',
          description: '–°–º–µ—à–∞—Ç—å —Ç–µ—Å—Ç–æ –∏ —Ä–∞—Å–∫–∞—Ç–∞—Ç—å –Ω–∞ –ø—Ä–æ—Ç–∏–≤–Ω–µ, –ø–æ—Å—Ç–∞–≤–∏—Ç—å –≤ –¥—É—Ö–æ–≤–∫—É –∑–∞—Ä–∞–Ω–µ–µ —Ä–∞–∑—Ä–≥—Ä–µ—Ç—É—é –¥–æ –º–∞–∫—Å–∏–º—É–º–∞. –ü–æ–¥–µ—Ä–∂–∞—Ç—å 10 –º–∏–Ω—É—Ç –ø–æ–∫–∞ —Ç–µ—Å—Ç–æ –Ω–µ —Å—Ç–∞–Ω–µ—Ç —Å–ª–µ–≥–∫–∞ —Ç–≤–µ—Ä–¥–æ–≤–∞—Ç—ã–º.',
          time: 600
        },
        {
          title: '–ü–æ–¥–≥–æ—Ç–æ–≤–∏—Ç—å –æ—Å–Ω–æ–≤—É',
          description: 'E—Å—Ç—å –¥–≤–∞ –≤–∞—Ä–∏–∞–Ω—Ç–∞: <ol> <li>–í–∑—è—Ç—å –∫–µ—Ç—á—É–ø, –±–∞—Ä–±–µ–∫—é, –≥–æ—Ä—á—Ü—É, –ø–µ—Ä–µ—Ü –∏ –±–∞–∑–∏–ª–∏–∫, –≤—Å–µ –ø–µ—Ä–µ–º–µ—à–∞—Ç—å;</li> <li>–û–±–∂–∞—Ä–∏—Ç—å –ª—É–∫ –∏ –ß–µ—Å–Ω–æ–∫, –¥–æ–±–∞–≤–∏—Ç—å —Ç—É–¥–∞ —Ç–æ–º–∞—Ç–Ω—É—é –ø–∞—Å—Ç—É –∏ –±–∞–∑–∏–ª–∏–∫.</li> </ol>'
        },
        {
          title: '–°–¥–µ–ª–∞—Ç—å –Ω–∞—á–∏–Ω–∫—É',
          description: '–°–º–∞–∑—ã–≤–∞–µ—à—å —Ç–µ—Å—Ç–æ –æ—Å–Ω–æ–≤–æ–π, –ø–æ—Å—ã–ø–∞–µ—à—å –Ω–∞—Ç–µ—Ä—Ç–æ–π –º–æ—Ü–∞—Ä–µ–ª–ª–æ–π (–º–æ–∂–Ω–æ –¥–æ–±–∞–≤–∏—Ç—å –ª—é–±–æ–π –¥—Ä—É–≥–æ–π —Å—ã—Ä). –°–≤–µ—Ä—Ö—É –∫–ª–∞–¥—ë—à—å —Ç–æ–Ω–∫–æ –Ω–∞—Ä–µ–∑–∞–Ω–Ω—É—é –∫–æ–ª–±–∞—Å—É, –æ–ª–∏–≤–∫–∏, —Ä–∞–∑—Ä–µ–∑–∞–Ω–Ω—ã–µ –ø–æ–ø–æ–ª–∞–º, –ø–æ–º–∏–¥–æ—Ä–∫–∏ (–ª–∏–±–æ —á–µ—Ä–∏-–ø–æ—Ä–µ–∑–∞—Ç—å –ø–æ–ø–æ–ª–∞–º –∏ —Å—Ä–µ–∑–æ–º –≤–Ω–∏–∑, –ª–∏–±–æ –≤ —Å–æ–±—Å—Ç–≤–µ–Ω–Ω–æ–º —Å–æ–∫—É –∏–∑ –±–∞–Ω–∫–∏). –°–≤–µ—Ä—Ö—É –º–æ–∂–Ω–æ –¥–æ–±–∞–≤–∏—Ç—å –º–æ—Ü–∞—Ä–µ–ª–ª—É.'
        },
        {
          title: '–ó–∞–ø–µ—á—å',
          description: '–°—Ç–∞–≤–∏—à—å –≤ –¥—É—Ö–æ–≤–∫—É –¥–æ —Ç–æ–≥–æ, –∫–∞–∫ —Å—ã—Ä —Ä–∞—Å—Ç–µ—á–µ—Ç—Å—è –∏ –±—É–¥–µ—Ç –∫—Ä–∞—Å–æ—Ç–∞, —ç—Ç–æ –æ—Ç 5 –¥–æ 10 –º–∏–Ω—É—Ç (–∑–∞–≤–∏—Å–∏—Ç –æ—Ç –¥—É—Ö–æ–≤–∫–∏). –î–æ—Å—Ç–∞—ë—à—å, –ø–æ—Å—ã–ø–∞–µ—à—å –Ω–∞—Ç–µ—Ä—Ç—ã–º –ø–∞—Ä–º–µ–∑–∞–Ω–æ–º.',
          time: 300
        }
      ],
      nextTimer: 0
    };
  },
  methods: {
    handleClick(index) {
      this.parts[index].complete = !this.parts[index].complete;
      if (index < this.parts.length - 1 && this.parts[index + 1].time) {
        this.nextTimer = this.parts[index + 1].time
      }
    }
  },
  components: { part: RecipePart, Timer },
  computed: {
    totalSteps() {
      return this.parts.reduce((count, part) => part.complete ? count + 1 : count, 0);
    },
    complete() {
      return this.parts.every(part => part.complete);
    }
  }
}
</script>
